@import 'styles/base.scss';

.repo-card-root {
    height: 400px;
    width: 300px;
    margin: 2 * $theme--spacing;
    display: block;
    flex-shrink: 1;

    @include respond-below(340px) {
        width: calc(100vw - 76px);
    }

    .repo-card {
        width: 100%;
        height: 100%;
        border-radius: 2 * $theme--spacing;
        padding: 0px;
        display: flex;

        .content {
            flex: 1 1 auto;
            min-width: 0;
            padding: 3 * $theme--spacing;
            padding-right: 2 * $theme--spacing;
            display: flex;
            flex-direction: column;

            @include respond-below(sm) {
                padding: 2 * $theme--spacing;
                padding-right: 1 * $theme--spacing;
            }

            .content-header {
                display: flex;
                align-items: center;
                justify-items: center;
                margin-bottom: 1.5 * $theme--spacing;

                .icon-root {
                    margin-right: 0.6 * $theme--spacing;
                    display: flex;
                    align-items: baseline;

                    svg {
                        height: 16px;
                        width: 16px;
                        color: $primary-color-light;
                    }
                }

                .title-root {
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;

                    a {
                        text-decoration: none;

                        .title {
                            line-height: initial;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                    }

                    .subtitle {
                        color: $primary-color-light;

                        a {
                            text-decoration: none;
                        }
                    }
                }
            }

            .description {
                margin-bottom: 1 * $theme--spacing;
                flex: 1 1 auto;
                min-height: 0%;

                word-wrap: break-word;
                white-space: pre-line;
                overflow: hidden;

                @include respond-above(450px) {
                    overflow-y: scroll;
                }

                .description-body {

                    a {
                        text-decoration: none;

                        .description-body-heading {
                            text-decoration: underline dotted $primary-color-light 1px;
                            line-height: inherit;
                            color: $primary-color-light;
                            font-family: "SF Mono", 'Courier New', Courier, monospace, $default-font-families;
                        }
                    }


                    .description-readme {
                        h1 {
                            font-size: 20px;
                            margin: $theme--spacing 0px;
                        }

                        h2 {
                            font-size: 19px;
                            margin: 0.9 * $theme--spacing 0px;
                        }

                        h3 {
                            font-size: 18px;
                            margin: 0.8 * $theme--spacing 0px;
                        }

                        p {
                            font-size: 16px;
                            margin: 0.6 * $theme--spacing 0px;
                        }

                        ul {
                            margin: 0px;
                        }
                    }
                }
            }

            .topics {
                display: flex;
                flex-wrap: wrap;
                align-items: flex-start;
                justify-items: center;

                a {
                    text-decoration: none;
                }

                .topic {
                    display: inline;
                    padding: 0.2 * $theme--spacing 1 * $theme--spacing;
                    background-color: rgba($secondary-color-light, 0.15);
                    border-radius: 50vh;
                    margin-right: $theme--spacing;
                    margin-bottom: 1 * $theme--spacing;
                }
            }

            .content-footer {
                display: flex;

                .left {
                    @include respond-below(340px) {
                        flex: 1 0 auto;
                    }

                    .footer-buttons {
                        .github-star-icon, .github-fork-icon {
                            height: 16px;
                            width: 16px;
                        }

                        .icon-text {
                            margin-left: 0.5 * $theme--spacing;
                            margin-right: $theme--spacing;
                        }
                    }
                }

                .center {
                    flex: 1 0 auto;
                    display: flex;

                    @include respond-below(340px) {
                        display: none;
                    }

                    .archived-text {
                        display: inline;
                        padding: 0.2 * $theme--spacing 1 * $theme--spacing;
                        background-color: $ios-systemgray5;
                        border-radius: 50vh;
                        margin: auto;

                        a {
                            text-decoration: none;
                        }
                    }

                    .license-button {
                        display: flex;
                        padding: 0.2 * $theme--spacing 1 * $theme--spacing;
                        background-color: rgba($primary-color-light, 0.15);
                        border-radius: 50vh;
                        margin: auto;

                        .license-icon {
                            color: $primary-color;
                            height: 16px;
                            width: 16px;
                            margin-right: $theme--spacing;
                        }

                        .license-text {
                            color: $primary-color;
                            text-transform: none;
                        }
                    }
                }

                .right {
                    display: flex;
                    justify-items: center;
                    align-items: center;

                    .footer-buttons {
                        margin-left: $theme--spacing;

                        .github-icon {
                        }

                        .link-icon {
                            color: $primary-color-light;
                        }
                    }
                }
            }

        }
    }
}
